import React from "react";
import profilecss from './post.module.css';

const Post = (props) => {
    return (
        <div className={profilecss.item}>
            <img
                alt={'Avatar'} src={'http://cdn.forum.wotblitz.ru/wotb/ru/uploads/monthly_12_2018/post-74678117-0-31908900-1543839278.jpeg'}></img>
            {props.message}
            <div>
                <span>{props.likescount} Like(s)</span>
            </div>
            <div>
                <span>DisLike</span>
            </div>
        </div>
    )
};

export default Post;