import React from "react";
// import profileInfoCSS from './profileinfo.module.css';
import Preloader from "../../../Common/Preloader/preloader";

class ProfileStatus extends React.Component {
    state = {
        status: this.props.status,
        editMode: false
    }

    activateEditMode () {
        this.setState({
            editMode: true
        })
    }

    deActivateEditMode () {
        this.props.setUserStatus(this.state.status)
        this.setState({
            editMode: false
        })
    }

    changeStatus = (event) => {
        this.setState({
            status: event.target.value
        })
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.status !== this.props.status) (
        this.setState({
            status: this.props.status
        }));
    }


    render() {
        if (!this.state.status) {
            return <Preloader/>
        }
        return (
            <div>
                {!this.state.editMode &&
                <div>
                    <span onDoubleClick={this.activateEditMode.bind(this)}>{this.state.status || 'No status'}</span>
                </div>
                }
                {this.state.editMode &&
                    <div>
                        <input autoFocus={true} onChange={this.changeStatus} onBlur={this.deActivateEditMode.bind(this)} value={this.state.status}/>
                    </div>
                }
            </div>
        )
    }
}

export default ProfileStatus;