import React from "react";
import mypostscss from './myposts.module.css';
import Post from "./Post/post";
import {maxLengthCreator, requiredField} from "../../../../utils/validators/validators";
import {Field, reduxForm} from "redux-form";
import {TextArea} from "../../../Common/FormControls/FormControls";
// import {addPostActionCreator, onPostChangeActionCreator} from "../../../../Redux/profileReducer";

let maxLength = maxLengthCreator(10)

const MyPostsForm = (props) => {

    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field
                    component={TextArea}
                    placeholder={"Post"}
                    name={"post"}
                    validate={[requiredField, maxLength]}
                />
            </div>
            <div>
                <button>Add Post</button>
            </div>
        </form>
    )
}

const ReduxMyPostsForm = reduxForm({
    form: 'messages'
})(MyPostsForm)

class MyPosts extends React.PureComponent {

    render() {
        console.log("RENDER");
        let postsHTML =
            this.props.postsData.map(post => {
                    return (<Post key={post.id} message={post.message} likescount={post.likescount}/>);
                }
            );


        let onSendButtonClick = (formData) => {
            this.props.addPost(formData.post);
        }

        // let onPostChange = () => {
        //     let Text = NewPostText.current.value;
        //     props.onPostChange(Text);
        // }

        return (
            <div className={mypostscss.myposts}>
                <h3>My posts</h3>
                <div>
                    <ReduxMyPostsForm onSubmit={onSendButtonClick}/>
                </div>
                <div className={mypostscss.posts}>
                    {postsHTML}
                </div>
            </div>
        )
    }
}

export default MyPosts;