import React from "react";
import {connect} from "react-redux";
import Profile from "./profile";
import {
    getUserProfileThunkCreator,
    getUserStatusThunkCreator,
    setUserStatusThunkCreator
} from "../../../Redux/profileReducer";
import {withRouter} from "react-router-dom";
// import withAuthRedirectComponent, {withAuthRedirect} from "../../../HOC/withAuthRedirectComponent";
import {compose} from "redux";


class ProfileAPIComponent extends React.Component {

    componentDidMount() {
        let userId = this.props.match.params.userId;
        if (!userId) {
            userId = this.props.authorizedId;
        }
        this.props.getUserProfile(userId);
        this.props.getUserStatus(userId);
    }

    render() {
        if (!this.props.authorizedId) {return (<div>Авторизируйтесь</div>)};
        return (
            <>
                <Profile {...this.props} profile={this.props.profile}/>
            </>
        )
    }


}

// let ConnectedAuthRedirect = withAuthRedirect(ProfileAPIComponent);

let mapStateToProps = (state) => ({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    authorizedId: state.auth.userId,
    isAuth: state.auth.isAuth
});


// let WithUrlDataContainerComponent = withRouter(ConnectedAuthRedirect);

export default compose(
    connect(mapStateToProps,
        {
            getUserProfile: getUserProfileThunkCreator,
            getUserStatus: getUserStatusThunkCreator,
            setUserStatus: setUserStatusThunkCreator
        }
    ),
    withRouter,
    // withAuthRedirect,

)(ProfileAPIComponent)

